.time-control {
  width: 50%;
  display: flex;
  input {
    width: 100%;
    -webkit-appearance: none;
    cursor: pointer;
  }
  p {
    padding: 0 1rem;
  }
}

.player {
  min-height: 20vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.play-control {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  width: 50%;
  svg {
    cursor: pointer;
  }
}


input[type='range']::-webkit-slider-thumb{
  -webkit-appearance: none;
  height: 16px;
  width: 16px;
}

input[type='range'] {
  background: transparent;
}

.track {
  background: lightblue;
  width: 100%;
  height: 1rem;
  position: relative;
  border-radius: 1rem;
  overflow: hidden;
}

.animate-track {
  background-color: rgb(204, 204, 204);
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  pointer-events: none;
}